import React, { Component } from 'react';
import logo from "../assets/img/logo.svg";
import { loginCredential } from '../constant/login'
class Login extends Component {

    constructor(props){
        super(props)
        this.state={
            email: '',
            password: '',
            error:''
        }
    }
    // componentDidMount() {
    //     document.getElementsByTagName('body')[0].className = 'auth-wrapper';
    // }

    handleChange=(e)=>{
        console.log(e.target.name)
        console.log(e.target.value)
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    handleLogin=(e)=>{
        e.preventDefault()
        console.log(this.state.email)
        console.log(this.state.password)
        console.log(loginCredential)
        let LoggedIn = false;
        loginCredential.map((value)=>{
            if(value.email === this.state.email && value.password === this.state.password){
                localStorage.setItem("logged", value.email)
                LoggedIn = true
            }
        })

        if(LoggedIn){
            this.props.history.push('/dashboard')
        }else{
            this.setState({
                error: "Invalid Credential"
            })
        }
    }
    
    render() {
        return (
                <div className="all-wrapper menu-side with-pattern">
                    <div className="auth-box-w">
                        <div className="logo-w">
                            <a href="#/"><img alt="" src={logo} /></a>
                        </div>
                        <h4 className="auth-header">Login</h4>
                        <form>
                            <div className="form-group">
                                <label>Username</label>
                                <input className="form-control" name="email" placeholder="Enter your username" onChange={this.handleChange} />
                                <div className="pre-icon os-icon os-icon-user-male-circle" />
                            </div>
                            <div className="form-group">
                                <label>Password</label>
                                <input className="form-control" name="password" placeholder="Enter your password" type="password" onChange={this.handleChange} />
                                <div className="pre-icon os-icon os-icon-fingerprint" />
                            </div>
                            <span style={{color: 'red'}}>{this.state.error}</span>
                            <div className="buttons-w">
                                
                                <button className="btn btn-primary" onClick={this.handleLogin}>Log me in</button>
                                <div className="form-check-inline">
                                    <label className="form-check-label">
                                        <input className="form-check-input" type="checkbox" />Remember Me</label>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
        );
    }
}

export default Login;