import React, { Component } from 'react';
import logo from "../assets/img/logo.svg";
import avatar from "../assets/img/avatar1.jpg";
import doc from "../assets/img/doc.svg";
import pdf from "../assets/img/pdf.svg";
import { loginCredential } from '../constant/login'
import { Document, Page } from 'react-pdf/dist/esm/entry.webpack';
import { dashboardData, dashboardDataRajesh, dashboardDataRaj, dashboardDataAbhijith, dashboardDataDinesh } from '../constant/dashboard'
import SUPPLIEMENTAL from '../SUPPLEMENTAL.pdf'


class Dashboard extends Component {
    constructor(props){
        super(props)
        this.state={
            credential : '',
            docView: false
        }
    }

    logOut=()=>{
        localStorage.removeItem("logged")
        this.props.history.push('/login')
    }

    componentDidMount() {
        let logged = localStorage.getItem("logged");
        console.log(logged)
        loginCredential.map((value)=>{
            if(logged === value.email){
                this.setState({
                    credential: value
                })
            }
        })
        
        document.getElementsByTagName('body')[0].className = 'menu-position-side menu-side-left full-screen';
    }

    downloadPdf(data){
           var link = document.createElement('a');
            link.href = SUPPLIEMENTAL;
            link.download = data.name;
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
    }

    handlePdfView(data){
        this.setState({
            docView: true
        })
    }

    onDocumentLoadSuccess=(e)=>{
        console.log("{comes true")
        console.log(e)
    }

    handleChangeData(){
        let data = dashboardData
        if(this.state.credential.name === 'Rajesh Kannan'){
            data = dashboardDataRajesh
        }else if(this.state.credential.name === 'Raj'){
            data = dashboardDataRaj
        }else if(this.state.credential.name === 'Abhijith'){
            data = dashboardDataAbhijith
        }else{
            data = dashboardDataDinesh
        }
        return data;
    }

    

    render() {
        return (
                <div className="all-wrapper solid-bg-all">
                    <div className="layout-w">
                        <div className="menu-mobile menu-activated-on-click color-scheme-dark">
                            <div className="mm-logo-buttons-w">
                                <a className="mm-logo" href="#/"><img src={logo} alt=""/></a>
                                <div className="mm-buttons">
                                    <div className="content-panel-open">
                                        <div className="os-icon os-icon-grid-circles" />
                                    </div>
                                    <div className="mobile-menu-trigger">
                                        <div className="os-icon os-icon-hamburger-menu-1" />
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="menu-w color-scheme-light color-style-default menu-position-side menu-side-left menu-layout-compact sub-menu-style-over sub-menu-color-bright selected-menu-color-light menu-activated-on-hover">
                            <div className="logo-w">
                                <a className="logo" href="#/">
                                    <div className="logo-element"><img src={logo} alt=""/></div>
                                </a>
                            </div>
                            <h1 className="menu-page-header">Page Header</h1>
                            <ul className="main-menu">
                                <li className="sub-header"><span>DASHBOARD</span></li>
                                <li className="selected">
                                    <a href="#">
                                        <div className="icon-w">
                                            <div className="os-icon os-icon-layout" />
                                        </div><span>Documents</span></a>
                                </li>
                                <li className="selected">
                                    <a href="#">
                                        <div className="icon-w">
                                            <div className="os-icon os-icon-layout" />
                                        </div><span>Account</span></a>
                                    
                                </li>
                               {this.state.credential.name === "Raj" && <li className="selected">
                                    <a href="#">
                                        <div className="icon-w">
                                            <div className="os-icon os-icon-layout" />
                                        </div><span>Settings</span></a>
                                    
                                </li>}
                                {this.state.credential.name === "Raj" && <li className="selected">
                                    <a href="#">
                                        <div className="icon-w">
                                            <div className="os-icon os-icon-layout" />
                                        </div><span>Access Level</span></a>
                                    
                                </li>}
                            </ul>
                            
                        </div>
                        <div className="content-w">
                            <div className="top-bar color-scheme-light">
                                <div className="top-menu-controls">
                                    <div className="element-search autosuggest-search-activator">
                                        <input placeholder="Start typing to search..." />
                                    </div>
                                    <div className="messages-notifications os-dropdown-trigger os-dropdown-position-left"><i className="os-icon os-icon-mail-14" />
                                        <div className="new-messages-count">12</div>
                                    </div>
                                    <div className="top-icon top-settings os-dropdown-trigger os-dropdown-position-left"><i className="os-icon os-icon-ui-46" />
                                        <div className="os-dropdown">
                                            <div className="icon-w"><i className="os-icon os-icon-ui-46" /></div>
                                        </div>
                                    </div>
                                    <div className="logged-user-w">
                                        <div className="logged-user-i">
                                            <div className="avatar-w"><img alt="" src={avatar} /></div>
                                            <div className="logged-user-menu color-style-bright">
                                                <div className="logged-user-avatar-info">
                                                    <div className="avatar-w"><img alt="" src={avatar} /></div>
                                                    <div className="logged-user-info-w">
                                                        <div className="logged-user-name">{this.state.credential.name}</div>
                                                        <div className="logged-user-role" style={{ color: '#fff' }}>
                                                            {this.state.credential.name === 'Rajesh Kannan' ?
                                                            "LRA | LRB" :
                                                            this.state.credential.name === 'Raj' ?
                                                            "LRA | LRB | SRA | SRB" : 
                                                            this.state.credential.name === 'Abhijith' ?
                                                            "LRB | SRB" :
                                                            "LRA | LRB | SRA" 
                                                        }
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="bg-icon"><i className="os-icon os-icon-wallet-loaded" /></div>
                                                <ul>
                                                    <li onClick={this.logOut}><a href="#"><i className="os-icon os-icon-signs-11" /><span>Logout</span></a></li>
                                                </ul>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="content-i">
                                <div className="content-box">
                                    <div className="row pt-4">
                                        <div className="col-sm-12">
                                            <div className="element-wrapper">
                                                <h6 className="element-header">Documents</h6>
                                                
                                                <div className="row">
                                                    {this.handleChangeData().map((value)=>{
                                                        if((value.edit === this.state.credential.edit && this.state.credential.edit) || (value.alert === this.state.credential.alert && this.state.credential.alert) || (this.state.credential.read && value.read === this.state.credential.read) ||(this.state.credential.sign && value.sign === this.state.credential.sign) || (this.state.credential.download && value.download === this.state.credential.download))
                                                        return <div className="col-md-4">
                                                        <div className="element-box el-tablo padded"><h5 className="form-header">{value.name}
                                                        <img src={pdf} style={{ width: '35px' }} alt="" className="float-right" /></h5><div className="form-desc" />
                                                            <div className="el-chart-w">
                                                                <div className="chartjs-size-monitor">
                                                                    <div className="chartjs-size-monitor-expand">
                                                                    {this.state.credential.read && value.read &&<a className="badge badge-success" href="#" onClick={()=>{this.handlePdfView(value)}}>Read</a>}
                                                                        {this.state.credential.edit && value.edit && <a className="badge badge-primary" href="#">Edit</a>}
                                                                        {this.state.credential.alert && value.alert && <a className="badge badge-danger" href="#">Alert</a>}
                                                                        {this.state.credential.download && value.download && <a className="badge badge-warning" href="#" onClick={()=>{this.downloadPdf(value)}} >Download</a>}
                                                                        {this.state.credential.sign && value.sign && <a className="badge badge-dark" href="#">Sign</a>}
                                                                    </div>
                                                                    <div className="chartjs-size-monitor-shrink">
                                                                        <div className>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div></div>
                                                    </div>
                                                    })}
                                                    
                                                </div>
                                              </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    
                    <div className="display-type" />
                </div>
        );
    }
}

export default Dashboard;